import React from 'react'
import { graphql, Link } from 'gatsby'
import { GatsbyImage, getImage } from 'gatsby-plugin-image'

const IllustrationsPage = ({data}) => {
  const { allMdx: { nodes: illustration }} = data
  return (
    <section className='grid grid-cols-1 gap-8 lg:grid-cols-1 mt-28'>
      {illustration.map((item) => {
        const { id, frontmatter: {thumb, title, subtitle, slug} } = item
        return <article key={id} className='h-144 min-h-full aspect-w-1 aspect-h-1 w-full overflow-hidden bg-black relative'>
          <Link to={`/illustrations/${slug}`}>
            <GatsbyImage image={getImage(thumb)} alt={title} objectFit='contain' className='thumbnail hover:scale-105 transition duration-500 ease-in-out absolute hover:opacity-40' />
            <div className='thumbnail-hover-detail relative top-0 left-0 mt-56 pointer-events-none transition duration-500 ease-in-out'>
              <h1 className='font-bold text-8xl text-white text-center tracking-wider'>{title}</h1>
              <h3 className='font-regular text-3xl text-white text-center mt-8 tracking-wider'>{subtitle}</h3>
            </div>
          </Link>
        </article>
      })}
    </section>
  )
}

export const query = graphql`
  query {
    allMdx(sort: {frontmatter: {date: DESC}}) {
      nodes {
        id
        frontmatter {
          title
          subtitle
          thumb {
            childImageSharp {
              gatsbyImageData
            }
          }
          author
          slug
          date
        }
      }
    }
  }
`

export default IllustrationsPage
